import {FC} from "react";
import {Textarea} from "@/components/ui/textarea";
import {Button} from "@/components/ui/button";
import {ArrowUp} from "lucide-react";
import {Controller, useForm} from "react-hook-form";
import {PromptFormType} from "@/components/Chat/PromptForm/type";
import {PROMPT_FORM_INITIAL_VALUE, PROMPT_FORM_VALIDATION_SCHEMA} from "@/components/Chat/PromptForm/config";
import {yupResolver} from "@hookform/resolvers/yup";

const PromptForm: FC = () => {
    const {control, reset, handleSubmit} = useForm<PromptFormType>({
        defaultValues: PROMPT_FORM_INITIAL_VALUE,
        resolver: yupResolver(PROMPT_FORM_VALIDATION_SCHEMA)
    })

    const onSubmit = handleSubmit((values: PromptFormType) => {
        console.log(values);
        reset();
    })

    return (
        <form onSubmit={onSubmit}
              className="flex justify-center items-end gap-2 max-w-[50%] w-full bg-dark-bg-primary rounded-xl p-1 pb-3">
            <Controller
                render={({field}) => (
                    <Textarea
                        {...field}
                        name="prompt"
                        className="border-0 focus-visible:ring-0 resize-none"
                        placeholder="Введите запрос..."
                    />
                )}
                name="prompt"
                control={control}
            />

            <Button type="submit"
                    className="p-2 rounded-full w-fit bg-dark-text-primary hover:bg-dark-text-secondary mr-2"
            >
                <ArrowUp size={18} className="stroke-2 stroke-dark-bg-primary"/>
            </Button>
        </form>
    );
};

export default PromptForm;
